@use "../variables" as v;
@use "../mixin" as m;
/*==================BLOG DETAILS ARE===================*/

.blog-area{
  position: relative;
  overflow: hidden;
  padding-top: 135px;
  padding-bottom: 135px;

  &.for-blog{
    padding-bottom: 110px;
    
    @include m.lg{    
      padding-top: 110px;
      padding-bottom: 100px;
    }
    @include m.md{
      padding-top: 30px;
      padding-bottom: 80px;
    }

    @include m.sm{
     padding-top: 30px;
      padding-bottom: 80px;
    }

  }

  @include m.lg{
     padding: 100px 0;
    padding-bottom: 100px;
  }
  @include m.md{
     padding: 80px 0;
    padding-bottom: 80px;
  }
  @include m.sm{
     padding: 80px 0;
    padding-bottom: 80px;
  }

  

  img {
    max-width: 100%;
    height: auto;
  }

  .blog-details-wrapper{

    .aligncenter {
      clear: both;
      display: block;
      margin-left: auto !important;
      margin-right: auto !important;
      text-align: center;
    }

    .alignright {
      display: inline;
      float: right;
      margin-left: 1.5em !important;
    }

    .wp-caption img[class*=wp-image-] {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
  
      .single-post-item {  

        margin-bottom: 60px;

        iframe{
          width: 100%;
          height: 522px;
        }

        .post-content-wrapper {
          .post-meta {
            display: flex;
            align-items: center;
            gap: 25px;
            
            a {
              color: v.$gray-900;
            }
            .author-img,
            .blog-category,
            .post-date,
            .post-comment {
                img{
                    height: 40px;
                    width: 40px;
                    border-radius: 50%;
                    margin-right: 4px;
                }
              a {
                color: v.$gray-800;
              }
              svg {
                path {
                  fill: v.$primary-color;
                }
              }
              .meta-text {
                font-weight: v.$font-weight-regular;
                margin-left: 3px;
              }
          }
          .blog-category{
            @include m.xs{
              display: none;
            }
          }
          }
          .post-heading{
            margin-top: 20px;

            .post-title{        
              font-size: 40px;
              color: #07000e;
              font-weight: 700;
      
              @include m.lg{
                font-size: 34px;
              }
              @include m.sm{
                font-size: 32px;
              }
              @include m.xxs{
                font-size: 25px;
              }
            }
          }
          .post-content {

            .wp-block-heading{
              margin-bottom: 10px;
              font-weight: 600;
            }
    
            p{
              margin-top: 15px;
              margin-bottom: 15px;
              line-height: normal;

              @include m.xxs{
                font-size: 16px;
              }

              a{
                color: v.$primary-color;
              }

              strong{
                // font-size: 32px;
                // font-weight: v.$font-weight-bold;
                color: #07000e;
      
                @include m.md{
                  font-size: 28px;
                }
                @include m.sm{
                  font-size: 26px;
                }
                @include m.xs{
                  font-size: 24px;
                }
                @include m.xxs{
                  font-size: 22px;
                }
              }
              code{
                color: v.$primary-color;
              }

              em{
                strong{
                  font-size: 16px;
                }
              }
              &.blog-imgs{
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-top: 20px;
                flex-wrap: wrap;
                img{
                  max-width: 100%; 
                  height: auto;     
                  display: block;   
                }
              }
              img{
                border-radius: 20px;
                margin-top: 20px;
                margin-right: 30px;

                &:last-child{
                  margin-right: 0;
                }
              }

              &.has-text-color{
                strong{
                    color: #1e0566;
                    font-size: 16px;
                }
              }

              &.has-small-font-size {
                font-size: 13px !important;
                line-height: 24px;
                margin-bottom: 20px;
              }

              &.has-medium-font-size {
                  font-size: 20px !important;
                  line-height: 32px;
                  margin-bottom: 20px;
              }

              &.has-large-font-size {
                  font-size: 36px !important;
                  line-height: 48px;
                  margin-bottom: 20px;
              }

              &.has-x-large-font-size {
                  font-size: 42px !important;
                  margin-bottom: 20px;
              }

              &.has-drop-cap.has-medium-font-size{
                strong{
                  font-size: 20px;
                  color: #616670;
                }
              }
              &.has-very-light-gray-color {
                  color: #eee;
                  background: v.$primary-color !important;
              }
            }
            ul {
              margin-top: 20px;
              margin-bottom: 20px;
              li {
                margin-top: 10px;
                color: v.$gray-800;
                font-size: 18px;
                font-weight: v.$font-weight-medium;
                margin-right: 8px;
                position: relative;    
                
                code{
                  color: v.$primary-color;
                }

                a{
                  color: v.$primary-color;
                }
                
              }
            }
            

            h2{
              margin-bottom: 10px;
              font-weight: 600;

            

              &.has-small-font-size {
                font-size: 13px !important;
                line-height: 24px;
                margin-top: 25px;
              }

              &.has-medium-font-size {
                  font-size: 20px !important;
                  line-height: 32px;
              }

              &.has-large-font-size {
                  font-size: 36px !important;
                  line-height: 48px;
              }

              &.has-x-large-font-size {
                  font-size: 42px !important;
              }
            }
            h3, h4, h5, h6{
              margin-bottom: 10px;
              font-weight: 600;
            }

            .wp-block-group {
                margin: 15px 0;

                &:where(.wp-block-group.has-background) {
                    padding: 1.25em 2.375em;
                    
                    p{
                      color: black;
                    }
                }

                p{
                  margin: 0;
                  font-size: 16px;
                  

                  strong{
                    font-size: 16px;
                  }
                }
            }
            .wp-block-navigation__container {
              &.wp-block-navigation{
                .wp-block-page-list{
                  a{
                    color: #616670;
                  }

                  ul{
                    li{
                      a{
                        color: black;
                      }
                    }
                  }
                }
              }
            } 

            .wp-block-pages-list__item {
              &.wp-block-navigation-item{
                &:before{
                  display: none;
                }
              }
            }

            .wp-block-navigation__responsive-container-content{
              ul{
                margin: 0;
                padding-left: 20px;

                li{
                  & ul{
                    margin: 0;
                    li{
                      color: black;
                      line-height: 30px;
                    }
                  }
                }
              }
            }

            h1{
              font-size: 68px;
              line-height: 78px;
              font-weight: 700;
              margin-top: 0;
              color: black;
              transition: all 0.3s;

              &:hover{
                a{
                  color: v.$primary-color;
                }
              }
            }

            .wp-block-query {

              .wp-block-post-template {
                  padding-left: 0;
                  margin-left: 0;
                  margin-top: 0;

                  .wp-block-post {
                    margin-bottom: 30px;

                    &::before{
                      display: none;
                    }

                    h2{
                      font-weight: 700;
                      color: black;
                      word-break: break-word;
                    }
                  }
              }
            }

            .wp-block-query-pagination{
              .wp-block-query-pagination-numbers{
                    display: inline-flex;
                    align-items: center;
                    gap: 12px;
                    flex-wrap: wrap;
                    margin-bottom: 10px;
                .page-numbers{
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  color: #23262D;
                  background-color: rgba(217, 161, 76, 0.1);
                  border-radius: 50%;
                  height: 67px;
                  width: 67px;
                  transition: all 0.5s ease-out;
                  font-size: 24px;
                  font-weight: 700;
                  text-transform: capitalize;

                  &:hover,
                  &.current{
                    background-color: v.$primary-color;
                    color: white;
                  }
                }
              }

              &-next{
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  color: #616670;
                  background-color: #ffffff;
                  height: 53px;
                  padding: 0 20px;
                  transition: all 0.5s ease-out;
                  font-size: 18px;
                  font-weight: 500;
                  border: 1px solid rgba(44, 53, 62, 0.1490196078);
                  text-transform: capitalize;

                  &:hover{
                    color: white;
                    background-color: v.$primary-color;
                  }
              }
            }

            .wp-block-post-terms,
            .wp-block-post-navigation-link,
            .wp-block-read-more{
              color: v.$primary-color;

              a{
                color: v.$primary-color;
              }
            }

            .blog-coment-title,
            .wp-block-comments {
              h2,
              .wp-block-comments-title {
                
                margin-bottom: 0;
                font-weight: 500;
                margin-top: -10px;
               
              }
            }
            .wp-block-comments {
              padding-left: 0;

              .wp-block-comment-template li .wp-block-columns {
                  padding: 50px;
                  border: 1px solid #dfe2e9;
                  margin-bottom: 24px;
              }
            }
            .wp-block-avatar {
                flex-shrink: 0;
                border-radius: 50%;
                overflow: hidden;

                img {
                  width: 100px;
                  height: 100px;
                }
            }
            
            .comment-form{

              .comment-respond .contacts-message {
                  margin-bottom: 10px;
              }

            }
            .wp-block-archives{
              li{
                a{
                  color: #616670;
                  transition: all 0.3s;

                  &:hover{
                    color: v.$primary-color;
                  }
                }
              }
            }
            .wp-block-categories{
              li{
                a{
                  color: v.$primary-color;
                }
              }
            }
            
            .wp-block-latest-posts__list{
              li{
                a{
                  color: black;
                  transition: all 0.3s;

                  &:hover{
                    color: v.$primary-color;
                  }
                }
              }
            }
            .wp-block-page-list{
              li{
                a{
                  color: v.$primary-color;
                }
              }
            }

            .wp-block-search {
                margin-bottom: 15px;
                width: 100%;
                display: inline-block;
                overflow: hidden;
            }

            .wp-block-social-links{
              li{
                width: auto;
                a{
                  font-size: 24px;
                  padding: .25em;
                }
              }

              &.is-style-logos-only{
                li{
                  margin: 0;
                  padding: 0;
                  display: inline-block;
                  
                  &:first-child{
                    a{
                      color: black !important;
                    }
                  }
                  &:last-child{
                    a{
                      color: v.$primary-color !important;
                    }
                  }

                }
              }

              &.is-style-pill-shape{
                li{
                  width: auto;
                  a{
                    padding-left: .66667em;
                    padding-right: .66667em;
                    width: unset;
                    height: unset;
                  }
                }
              }
            }

            .has-pale-cyan-blue-background-color{
              p{
                  color: black;
                }
            }
            .has-pale-pink-background-color{
                padding: 1.25em 2.375em;

              .wp-block-column{
                p{
                  color: black;
                }

              }
            }
            .page-links{
              display: inline-flex;
              align-items: center;
              gap: 12px;
              flex-wrap: wrap;
              margin-bottom: 10px;
              
              .post-page-numbers{
                display: flex;
                align-items: center;
                justify-content: center;
                color: #23262D;
                background-color: rgba(217, 161, 76, 0.1);
                border-radius: 50%;
                height: 67px;
                width: 67px;
                transition: all 0.5s ease-out;
                font-size: 24px;
                font-weight: 700;
                text-transform: capitalize;

                &:hover,
                &.current{
                  background-color: v.$primary-color;
                  color: white;
                }
              }
            }

            blockquote {
              overflow: hidden;
              margin: 40px 0;
              position: relative;
              z-index: 1;
              padding: 40px 60px !important;
              box-shadow: 0 0 60px 0 rgba(0, 0, 0, 0.05);
              background-color: #fff;
              border-left: 1px solid v.$primary-color;

              p{
                margin: 0;
                line-height: 30px;
                 strong{
                    font-size: 18px;
                    a{
                      text-decoration: underline !important;
                      color: v.$primary-color;
                    }

                    code{
                      color: v.$primary-color;
                    }
                  }
                  a{
                    color: v.$primary-color;
                  }
              }
            }

            .wp-block-pullquote {
                border-top: 2px solid v.$primary-color !important;
                border-bottom: 2px solid v.$primary-color !important;
                background-color: #f4f4f4;

                blockquote{
                  p{
                    margin: 0;
                    color: #616670;
                  }
                  cite{
                    color: #616670;
                  }
                }

                &.is-style-solid-color {
                    background-color: #F4F4F4 !important;

                  blockquote {
                    margin-left: auto;
                    margin-right: auto;
                    max-width: 60%;

                    &.has-very-dark-gray-color {
                          color: #313131;
                          margin-top: 0;
                          margin-bottom: 0;

                          p{
                            font-size: 1.6em;
                            color: black;
                          }
                      }
                  }
                }
            }

            .alignleft {
              display: inline;
              float: left;
              margin-right: 1.5em !important;
            }

            .wp-block-gallery{
              ul{
                margin: 0;
                padding: 0;
              }
            }
            .wp-block-columns{
              .wp-block-column{
                p{
                  strong{
                    font-size: 16px;
                  }
                }
              }
            }

            .gallery-columns-2 .gallery-item {
                max-width: 50%;
            }
            .gallery-item {
                display: inline-block;
                text-align: center;
                vertical-align: top;
                width: 100%;
                padding-right: 5px !important;
            }       
                        
            //select -month dropdown
              .nice-select{
                  width: 100%;
                  border-radius: 10px;
                  display: none;
              }
            
              // calender 
              .wp-calendar-table{
                  th{
                      background-color: transparent;
                  }

                  td#today{
                      background-color: v.$primary-color;
                      color: white;
                  }
              }

              nav.wp-calendar-nav {
                  justify-content: space-between;
                  display: flex;

                  span{
                    a{
                      text-decoration: none;
                      color: #616670;
                      font-weight: 500;
                      transition: all 0.3s;

                      &:hover{
                        color: v.$primary-color;
                      }
                    }
                  }

              }

              //legecy table
              .calendar_wrap{
                table{
                  width: 100%;
                  text-align: center;
                  td{
                        border: 1px solid #ddd;
                        padding: .25em;
                    }
                }
              }

            /* Select Option  */
            select {
              max-width: 100%;
              height: 60px;
              width: 200px;
              margin-bottom: 20px;
              border: 1px solid #E3E3E3;
              padding: 0 10px;
              border-radius: 5px;
              color: #616670;
              background-color: transparent;
              display: block !important;
              margin-top: 10px;

            }

            .wp-block-latest-comments__comment{
              margin-bottom: 0;

              .wp-block-latest-comments__comment-meta{
                .wp-block-latest-comments__comment-author,
                .wp-block-latest-comments__comment-link{
                  color: black;
                  transition: all 0.3s;

                  &:hover{
                    color: v.$primary-color;
                  }
                }
              }
            }

            .wp-block-latest-posts__list.has-dates.wp-block-latest-posts{
              li{
                a{
                  display: block;
                }
              }
            }

            .wp-block-tag-cloud{
              a{
                color: #616670;
                transition: all 0.3s;

                &:hover{
                  color: v.$primary-color;
                }
              }
            }

            .wp-block-button {
              margin-top: 20px;
              margin-bottom: 40px;
            }

            .wp-block-list{
              li{
                a{
                  color: v.$primary-color;
                }
              }
            }

            table {
              margin-bottom: 20px;
              width: 100%;
              th{
                a{
                  color: v.$primary-color;
                }
              }

              th, td {
                background-color: transparent;
                border: 1px solid #dfe2e9;
                text-align: center;
                padding: 8px;
                transition: 0.3s;
                color: #616670;
              }
            }
            dl{
              color: #616670 !important;
            }
            
          }
        }

        .default-single-post-meta {
         
          padding-top: 50px;
          width: 100%;
          display: flex;
          gap: 15px;
          flex-direction: column;

          &.style-2{
            flex-direction: row;
            align-items: center;
            justify-content: space-between;

            @include m.sm{
              flex-direction: column;
              align-items: flex-start;
            }

            @include m.xs{
              .blog-post-tag{
                margin-top: 0;
              }
            }
          }

          @include m.xs {
            flex-wrap: wrap;
          }
          .blog-post-cat{
            span{
              font-size: 32px;
              font-weight: v.$font-weight-bold;
              margin-bottom: 5px;

              @include m.md{
                font-size: 28px;
              }
              @include m.sm{
                font-size: 26px;
              }
              @include m.xs{
                font-size: 24px;
              }
              @include m.xxs{
                font-size: 22px;
              }
            }

            .post-cat-list{              
              gap: 5px;
              display: inline-flex;
              flex-wrap: wrap;
              margin-top: 5px;

              a{
                display: inline-flex;
                justify-content: center;
                align-items: center;
                padding: 4px 10px;
                border: 1px solid v.$border-color-04;
                @include m.border-radius(50px);
                color: v.$gray-800;
                font-size: 15px;
                font-weight: v.$font-weight-regular;
                &:hover {
                  background: v.$primary-color;
                  color: v.$white;
                }
              }
              
            }
          }
          
          .blog-post-tag{
            @include m.device-525px {
              margin-top: 40px;
            }
            span{
              font-size: 32px;
              font-weight: v.$font-weight-bold;
              margin-bottom: 5px;
              @include m.md{
                font-size: 28px;
              }
              @include m.sm{
                font-size: 26px;
              }
              @include m.xs{
                font-size: 24px;
              }
              @include m.xxs{
                font-size: 22px;
              }
            }
            .post-tag-list {
              display: inline-flex;
              align-items: center;
              flex-wrap: wrap;
              gap: 5px;
              
              a {                    
                display: inline-flex;
                justify-content: center;
                align-items: center;
                padding: 4px 10px;
                border: 1px solid v.$border-color-04;
                @include m.border-radius(50px);
                color: v.$gray-800;
                font-size: 15px;
                font-weight: v.$font-weight-regular;
                &:hover {
                  background: v.$primary-color;
                  color: v.$white;

                  svg{
                    path {
                      fill: white;
                    }
                  }
                }          
                svg {
                  font-size: 20px;  
                  padding: 2px  5px;                    
                  path {
                    fill: v.$primary-color;
                  }
                }
              }
            }
          }
          
        }
        
        &.format-image {    
          .post-thumbnail {
            margin-bottom: 20px;
            img {
              @include m.border-radius(20px);
              width: 100%;
            }
          }
        }
        &.single-video-post{
          .post-video {
            position: relative;
            img {
              @include m.border-radius(20px);
              width: 100%;
            }
            .video-btn{
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              text-align: center;
              font-size: 36px;
              color: #ffffff;
              cursor: pointer;
            }

            a{
              width: 48px;
              height: 48px;
              position: absolute;
              top: 50%;
              left: 50%;
              -webkit-border-radius: 50px;
              -moz-border-radius: 50px;
              border-radius: 50px;
              transform: translate(-50%, -50%);
              text-align: center;
              font-size: 16px;
              line-height: 48px;
              background: #573db8;
              color: #ffffff;
              cursor: pointer;
              opacity: 1;
              animation: 2s ease pulse infinite;
            }
          }
          .post-content-wrapper {
            .post-meta {
              display: flex;
              gap: 25px;
              align-items: center;
              margin-top: 20px;
              a {
                color: v.$gray-900;
              }
              @include m.lg{
                gap: 15px;
              }

              .author-img{
                img{
                  height: 40px;
                  width: 40px;
                  border-radius: 50%;
                  margin-right: 8px;

                  @include m.xxs{
                    height: 35px;
                    width: 35px;
                  }
                }
              }
              span{
                @include m.lg{
                  font-size: 15px;
                }
                svg{
                  color: v.$primary-color;
                  margin-right: 8px;
                  font-size: 18px;

                  @include m.lg{
                    margin-right: 5px;
                  }
                  @include m.xxs{
                    margin-right: 2px;
                  }
                }
                &:last-child{
                  @include m.sm{
                    display: none;
                  }
                }
              }
              .author-img,
              .blog-category,
              .post-date {
                a {
                  color: v.$gray-800;
                }
                svg {
                  
                    color: v.$primary-color;
                  
                }
                .meta-text {
                  font-weight: v.$font-weight-regular;
                  margin-left: 3px;
                }
              }
              .blog-category{
                @include m.xs{
                  display: none;
                }
              }
            }
            .post-title{        
              font-size: 40px;
              color: #07000e;
              font-weight: 700;
              margin-top: 20px;
      
              @include m.lg{
                font-size: 34px;
              }
              @include m.sm{
                font-size: 32px;
              }
              @include m.xxs{
                font-size: 25px;
              }
            }
            .post-content {
      
              p{
                padding-top: 20px;
                @include m.xxs{
                  font-size: 16px;
                }

                strong{
                  font-size: 32px;
                  font-weight: v.$font-weight-bold;
                  color: #07000e;
        
                  @include m.md{
                    font-size: 28px;
                  }
                  @include m.sm{
                    font-size: 26px;
                  }
                  @include m.xs{
                    font-size: 24px;
                  }
                  @include m.xxs{
                    font-size: 22px;
                  }
                }
                &.blog-imgs{
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
                  margin-top: 20px;
                  img{
