/* Block Comment Start */
.wp-block-comments {
  .wp-block-comment-template {
    padding-left: 0;
    .wp-block-comment-reply-link {
      margin-top: 15px;
    }
    li {
      .wp-block-columns {
        padding: 50px;
        border: 1px solid #dfe2e9;
        //border-radius: 8px;
        margin-bottom: 24px;

        .wp-block-column {
          &:first-child {
            flex-basis: 80px !important;
          }
        }
        .wp-block-avatar {
          width: 100px;
          flex-shrink: 0;
          border-radius: 50%;
          overflow: hidden;

          img {
            width: 100%;
          }
        }
        .wp-block-comment-author-name {
            a {
              font-size: 21px;
              margin-bottom: 5px;
              font-weight: 500;
              color: black;
            }
        }
        .wp-block-comment-edit-link {
          a {
            color: v.$primary-color;
          }
        }
        .wp-block-comment-date,
        .wp-block-comment-edit-link {
          a {
            color: #9ba8c3;
            font-size: 16px;
            font-weight: 400;
          }
        }
        .wp-block-comment-reply-link {
           a {
             //border-radius: 6px;
             font-size: 14px;
             font-weight: 500;
             color: black;
             border: 1px solid v.$primary-color;
             padding: 4px 26px;
             display: inline-block;

             &:hover {
               background-color: v.$primary-color;
               color: white;
               transition: all 0.3s;
             }
           }
        }
      }
    }
  }
}

/* Block Comment End */
.latest-comments {
  .comment-respond {
    margin-top: 30px;
  }
  li {
    &.comment {
      padding: 0;

      .comments-box {
        padding-top: 48px;
        padding-bottom: 13px;
        border-top: 1px solid rgba(44, 53, 62, 0.1490196078);

        blockquote {
          box-shadow: 0 0 60px 0 rgba(0, 0, 0, 0.05);
          background-color: white;
          margin-right: 40px;          
        }
      }


      .children {
        li {
          &.comment {
              .comments-box {
                border-top: 1px solid rgba(44, 53, 62, 0.1490196078);
              }
          }
        }
      }

      &:first-child {
        .comments-box {
          border-top: 0;
        }
      }
    }

    .children {
      padding-left: 0;

      .comments-box {
        margin-left: 106px;

        @include m.sm {
          margin-left: 0;
        }
      }
      
    }

    .comments-box {
     
      .comments-avatar {
        float: left;
        width: 104px;
        height: 104px;
        border-radius: 50%;
        overflow: hidden;
        position: relative;
        margin-right: 30px;

        @include m.sm {
          float: inherit;
          margin-bottom: 20px;
        }
        img {
          //margin-right: 23px;
          position: absolute;
          width: 100%;
          height: 100%;
          object-fit: cover;
          top: 0;
          left: 0;
        }
      }
      .comments-text {
        overflow: hidden;


        p {
          font-size: 16px;
          line-height: 30px;
          margin-bottom: 20px;
          color: #616670;

          a{
            color: v.$primary-color;
          }
        }

        table{
          td{
            a{
              color: v.$primary-color;
            }
          }
        }
        .avatar-name {
          margin-bottom: 10px;
          position: relative;

          h5 {
            font-size: 18px;
            margin-bottom: 5px;
            font-weight: 700;
            line-height: 27px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            
            a{
              color: black;
              transition: all 0.3s;

              &:hover{
                color: v.$primary-color;
              }
            }
          }
          span {
            color: #616670;
            font-size: 14px;
            font-weight: 400;

            @include m.sm {
              display: inline-block;
              width: 100%;
            }
          }
        }
        .comment-reply-link {
          display: inline-flex;
          align-items: center;
          font-size: 16px;
          font-weight: 500;
          color:black;
          border: 1px solid transparent;
          padding: 5px 24px;
          gap: 8px;
          position: absolute;
          top: 0;
          right: 0;
          i{
            color: v.$primary-color;
          }
          

          @include m.sm {
            position: unset;
            margin-top: 10px;
          }

          &:hover {
            background-color: v.$primary-color;
            color: white;
            transition: all 0.3s;
          }
        }
      }
    }

  }
}

